import Checkbox from '@/demos/checkboxdemo'//
import Breadcrumb from '@/demos/breadcrumbdemo' //
import WList from '@/demos/wSortListDemo' //
import previewImgsDemo from '@/demos/previewImgsDemo' //
import Rater from '@/demos/raterDemo' //
import City from '@/demos/city' // +
import Login from '@/components/login'
import download from '@/demos/downloadqrcode'
import Register from '@/components/register'
import Websoket from '@/components/websokettest.vue'
import previewImg from '@/components/previewImg.vue'
import upload from '@/components/upload.vue'
let routes = [
	{
	  path: '/login',
	  name: '',
	  component: Login
	},
	{
	  path: '/register',
	  name: '',
	  component: Register
	},
	{
	  path: '/soket',
	  name: 'soket',
	  component: Websoket
	},
	{
	  path: '/checkbox',
	  name: '',
	  component: Checkbox
	},
	{
	  path: '/breadcrumb',
	  name: '',
	  component: Breadcrumb
	},
	{
	  path: '/wlist',
	  name: '',
	  component: WList
	},
	{
	  path: '/previewimg',
	  name: '',
	  component: previewImg
	},
	{
	  path: '/previewimgDemo',
	  name: 'demo',
	  component: previewImgsDemo
	},
	{
	  path: '/upload',
	  name: '',
	  component: upload
	},
	{
	  path: '/download',
	  name: '',
	  component: download
	},
	{
	  path: '/rater',
	  name: '',
	  component: Rater
	},
	{
	  path: '/city',
	  name: '',
	  component: City
	}
]
export default routes



